<template>
  <div class="blogTab">
    <p class="searchTitle">
      <input type="text" placeholder="输入标题关键字" class="NormalInp"><span class="NormalBtn">搜索</span>
    </p>
    <div class="rightBox">
      <p class="top">分类标签</p>
      <ul class="blogType">
        <li v-for="tab in tabList">{{tab}}</li></ul>
    </div>
    <div class="rightBox">
      <p class="top">最新发布</p>
      <ul class="newList">
        <li>排序算法之冒泡排序 － java实现</li>
        <li>快速搭建基于二进制日志文件的 mysql 复制</li>
        <li>web 服务器负载均衡教程，快速搭建高可用服务器集群</li>
        <li>使用 redis 和 spring-session 实现 tomcat、glassfish 等 web 服务器集群 session 共享</li>
        <li>排序算法之冒泡排序 － java实现</li>
      </ul>
    </div>
    <div class="rightBox">
      <p class="top">最新评论</p>
      <ul class="newList">
        <li>
          <span>From：<a>原生JS实现瀑布流</a></span>
          <p>每次加载都是从第一个div开始加载，不浪费么</p>
        </li>
        <li>
          <span>From：<a>原生JS实现瀑布流</a></span>
          <p>添加图片的那个效果能不能给下html和cs</p>
        </li>
        <li>
          <span>From：<a>原生JS实现瀑布流</a></span>
          <p>很赞</p>
        </li>
        <li>
          <span>From：<a>使用gulp编译sass</a></span>
          <p>快吃饭了，写的比较赶，没有好好措辞，请见谅！...</p>
        </li>
        <li>
          <span>From：<a>使用gulp编译sass</a></span>
          <p>帮助CSS实现更多的功能</p>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
  import axios from 'axios'
  export default{
    name: 'BlogTab',
    data(){
      return{
        tabList: []
      }
    },
    methods: {
      getTab(){
        axios.get('/tab/getTab').then(res => {
          res.data.data.forEach(item => {
            this.tabList.push(item.tab_name)
          });
        }).catch(err => {
          console.log(err)
        });
      }
    },
    mounted(){
      this.getTab()
    }
  }
</script>


